<template>
  <div>
    <div
        style="width:100%;
            height:38px;
            background:rgba(13,67,111,1);
            box-shadow:0px 2px 10px 0px rgba(0, 0, 0, 0.05);
            font-size:14px;
            font-family:Microsoft YaHei;
            font-weight:400;
            color:rgba(255,255,255,1);
            padding-left:24px;
            line-height:38px;"
    >
      修改操作员
    </div>
    <Row>
      <Col>
        <Card style="margin:10px;" :style="{ height:tabHeight + 'px' }">
          <Row type="flex" justify="center" style="margin-top:20px;">
            <!-- <Col span="19" offset="2"> -->
            <Col>
              <Form ref="operatorUpdate" :model="operatorUpdate" :rules="operatorUpdateRule" label-position="right" :label-width="130">
                <FormItem label="名称" prop="operatorName">
                  <Input type="text" v-model="operatorUpdate.operatorName" :maxlength="20" :disabled="true" placeholder="请输入" style="width:466px;height:32px;"></Input>
                  <span style="color:rgba(175,188,204,1);margin-left:10px;">{{ operatorNameLength }}/20</span>
                </FormItem>
                <RadioGroup v-model="operatorUpdate.chooseAuthority" @on-change="selectedAuthority" style="padding-left:130px;padding-bottom:10px;">
                  <Radio label="通过角色赋权限">
                    <span style="color:rgba(255,255,255,1)">通过角色赋权限</span>
                  </Radio>
                  <Radio label="自定义权限" style="margin-left:70px;">
                    <span style="color:rgba(255,255,255,1)">自定义权限</span>
                  </Radio>
                </RadioGroup>
                <!-- 通过角色赋权限 -->
                <!-- <FormItem label="选择角色" prop="chooseRole"> -->
                <FormItem v-show="operatorUpdate.chooseAuthority==='通过角色赋权限'" label="选择角色" prop="chooseRole">
                  <RadioGroup v-model="operatorUpdate.chooseRole" @on-change="selectedRole" vertical :style="{height:tabHeight-210 +'px'}" style="overflow:auto;">
                    <div v-for="(item, index) in roleList" :key="index">
                      <Radio :label="item.roleId">
                        <span style="color:rgba(255,255,255,1)">{{item.name}}（{{item.description}}）</span>
                      </Radio>
                      <div v-show="operatorUpdate.chooseRole==item.roleId">
                        <Row >
                          <Col style="margin-left:30px;display:flex;align-items:flex-start">
                            <span style="color:rgba(255,255,255,1);">可访问的功能菜单</span>
                            <div 
                                style="width:300px;
                                height:240px;
                                border:1px solid rgba(87,111,140,1);
                                border-radius:2px;
                                overflow:auto;
                                margin-top:10px;
                                margin-left:10px;
                                background: #163C69;" 
                              >
                              <el-tree
                                id="adminUpdateTree"
                                ref="menuPermissionTree"
                                :data="menuPermission"
                                node-key="id"
                                :default-checked-keys="selectedItemPermissionsElement"
                                @check="(click, checked)=>{handleCheckChange(click, checked)}"
                                show-checkbox
                                default-expand-all>
                              </el-tree>
                            </div> 
                          </Col>
                        </Row>
                        <Row>
                          <Col style="margin-left:30px;display:flex;align-items:flex-start">
                            <span style="color:rgba(255,255,255,1);">可操作的权限</span>
                            <div style="margin-left:10px;">
                              <CheckboxGroup v-model="operatorUpdate.operationPermissionArray" @on-change="checkAllGroup" style="color:rgba(255,255,255,1);">
                                <Checkbox label="允许查看" disabled id="enableWatch"></Checkbox>
                                <Checkbox label="允许操作" disabled id="enableOperation"></Checkbox>
                              </CheckboxGroup>
                            </div>
                          </Col>
                        </Row>
                      </div>
                    </div>
                  </RadioGroup>
                </FormItem>
                <!-- 自定义权限 -->
                <FormItem v-show="operatorUpdate.chooseAuthority==='自定义权限'" label="可访问的功能菜单">
                  <div
                    style="width:464px;
                    height:260px;
                    border: 1px solid #576F8C;
                    border-radius:2px;
                    overflow:auto;
                    margin-top:10px;  
                    background: #163C69;"
                    :style="{height:tabHeight-300 + 'px'}"
                  >
                    <el-tree
                      id="adminCreateTree"
                      :data="menuPermission1"
                      node-key="id"
                      :default-checked-keys="selectedItemPermissionsElement1"
                      @check="(click, checked)=>{handleCheckChange(click, checked)}"
                      show-checkbox
                      default-expand-all>
                    </el-tree>
                  </div>
                </FormItem>
                <!-- 自定义权限 -->
                <FormItem v-show="operatorUpdate.chooseAuthority==='自定义权限'" label="可操作的权限">
                  <CheckboxGroup v-model="operatorUpdate.operationPermissionArray1" @on-change="checkAllGroup1" style="color:rgba(255,255,255,1);">
                    <Checkbox label="允许查看" disabled id="enableWatch"></Checkbox>
                    <Checkbox label="允许操作" id="enableOperation"></Checkbox>
                  </CheckboxGroup>
                </FormItem>
                <FormItem style="margin-left:120px;">
                  <Button @click="cancelUpdateOperator" style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);">取消</Button>
                  <Button type="primary" @click="confirmUpdateOperator('operatorUpdate')" style="margin-left:49px;">确定</Button>
                </FormItem>
              </Form>
            </Col>
          </Row> 
        </Card>
      </Col>
    </Row>
  </div>
</template>
<script>
import api from '@/api/api';

export default {
  data () {
    /** 修改操作员名称长度校验 */
    const validateOperatorNameLength = (rule, value, callback) => {
      if (!value) {
        this.operatorNameLength = 0;
        return callback(new Error("操作员名称不能为空!"));
      } else if (value.length > 20) {
        this.operatorNameLength = value.length;
        return callback(new Error("操作员名称过长,请重新输入!"));
      } else {
        this.operatorNameLength = value.length;
        callback();
      }
    };

    return {
      operatorUpdate:{
        operatorName:'', // 操作员名称
        chooseAuthority:'通过角色赋权限',  // 选择赋权限方式
        chooseRole:'',  // 选择的角色
        operationPermissionArray:['允许查看'], // 操作权限数组
        operationPermissionArray1:['允许查看'], // 操作权限数组（自定义权限）
      },
      operatorUpdateRule:{
        operatorName:[
          { required: true, message: '操作员名称不能为空!', trigger: 'blur' },
          { required: true, validator:validateOperatorNameLength, trigger: 'change' },
        ],
      },

      roleList:[], // 角色列表 数组
      operationPermissions:0, // 操作权限（通过角色赋权限），默认允许查看，0：不允许操作，1：允许操作
      operationPermissions1:0, // 操作权限（自定义权限），默认允许查看，0：不允许操作，1：允许操作
      roleIds:[], // 角色主键id 数组
      roleOnlyIds:[], // 角色唯一id 数组
      selectedItemPermissionsElement:[], // 默认勾选的节点的 key 的数组
      selectedItemPermissionsElement1:[], // 默认勾选的节点的 key 的数组（自定义权限）
      notLeaf:[], // 非叶子节点  数组(通过角色赋权限)
      notLeaf1:[], // 非叶子节点  数组（自定义权限）
      permissionIds:[], // 菜单主键id 数组
      permissionOnlyIds:[], // 菜单唯一id 数组
      choosedMenuIds:[], // 勾选菜单的主键id
      choosedMenuOnlyIds:[], // 勾选菜单的唯一id
      operatorNameLength:0, // 操作员名称长度
      selectedItemPermissions:[], // 角色所拥有的菜单权限主键id（通过角色赋权限）
      selectedItemNewPermissions:[], // 角色所拥有的菜单权限唯一id（通过角色赋权限）
      selectedItemPermissions1:[], // 角色所拥有的菜单权限主键id（自定义权限）
      selectedItemNewPermissions1:[], // 角色所拥有的菜单权限唯一id（自定义权限）
      isCheck:false, // 用户是否勾选过菜单，默认为false:没有勾选
      isCheck1:false, // 用户是否切换过 ‘权限’，默认为false：没有切换

      menuPermission: [], // 菜单权限树
      menuPermission1:[], // 空菜单权限树

      base:1,   // 分辨率自适应 计算后的倍数
      tabHeight:0,  // 分辨率自适应调整：表格的高度
             
    }
  },
  created(){
    // this.queryRoleList();
    // this.queryOperatorInfo();
    this.isCheck = false;

    this.getRoleStatus();
    // this.queryOperatorInfo1();
  },
  mounted(){
    this.autoFix();
  },
  methods:{

    /** 自适应 */
    autoFix(){
      let vm = this;
      this.getDom();
      window.onresize = function(){
        vm.getDom();
      }
    },

    /** 获取窗口的宽和高 */
    getDom(){
      let width = window.innerWidth;
      let height = window.innerHeight;
      this.base = (width-160)/(1024-160);
      // this.tabHeight = height - 350;  // 表格页面有“过滤搜索条件”card，表格上方有Button，则采用该方式计算。
      // this.tabHeight = height - 325;  // 表格页面有“过滤搜索条件”card，表格上方无Button，则采用该方式计算。
      // this.tabHeight = height - 275;  // 表格页面无 “过滤搜索条件”card,但表格上方有Button，则采用该方式计算。
      this.tabHeight = height - 130;   // 表格页面无 “过滤搜索条件”card,表格上方无Button，则采用该方式计算。
    },


    /** 获取当前角色的状态：通过角色赋权限 or 自定义权限 */
    getRoleStatus(){
      let operatorParam = JSON.parse(sessionStorage.getItem('operatorParam'));
      if (operatorParam) {
        if (operatorParam.roleStatus == 0) {  // 通过角色赋权限（永久角色）
          this.operatorUpdate.chooseAuthority = '通过角色赋权限';
          this.queryRoleList();
          this.queryOperatorInfo();
        } else if (operatorParam.roleStatus == 1) {  // 自定义权限（临时权限）
          this.operatorUpdate.chooseAuthority = '自定义权限';
          this.queryOperatorInfo1();
        }
      }
    },

    /** 选中的 '赋权限方式' */
    selectedAuthority(selectedData){
      this.operatorUpdate.chooseAuthority = selectedData;
      if (selectedData === '自定义权限') {
        this.getTheWholeTree2();
      } else if (selectedData === '通过角色赋权限') {
        this.queryRoleList();
        this.queryOperatorInfo();
      }
    },


    /** 查询角色列表（不分页,v2.0版本角色数量没有限制，所以不分页查询） */
    queryRoleList(){
      let param = {
        status:0,  // V2.0 版本 需过滤掉 临时角色（自定义权限）   0：查询 永久角色，1：查询 临时角色
      }

      api.roleSearchListByName(param).then(
        res => {
          if (res.result) {
            this.roleList = res.result;
          }
        }
      )
    },

    /** 查询操作员详情 */
    queryOperatorInfo(){
      let operatorParam = JSON.parse(sessionStorage.getItem('operatorParam'));

      if(operatorParam){
        let param = {
          id:operatorParam.id,
          useId:operatorParam.userId,
        }
        
        api.specificJson(param).then(
          res => {
            this.roleIds = [];
            this.roleOnlyIds = [];
            this.operatorUpdate.operatorName = res.result.name;
            this.operatorUpdate.chooseRole = res.result.roles[0].roleNewId;

            this.roleIds.push(res.result.roles[0].roleId);
            this.roleOnlyIds.push(res.result.roles[0].roleNewId);
            for(let item of this.roleList){
              if(item.roleId === res.result.roles[0].roleNewId){
                this.queryRolePermission(item);
                this.operatorUpdate.operationPermissionArray = [];
                this.operationPermissions = item.operationPermissions;
                if(this.operationPermissions === 1){
                  this.operatorUpdate.operationPermissionArray = ['允许查看','允许操作'];
                } else {
                  this.operatorUpdate.operationPermissionArray = ['允许查看'];
                }                 
              }
            }
            sessionStorage.setItem('operatorInfo', JSON.stringify(res.result))
          }
        )
      }
    },

    /** 选中的角色 */
    selectedRole(selectedData){
      // console.log(selectedData)
      if(selectedData){
        this.isCheck1 = true;
        let roleId;
        let roleOnlyId;
        this.roleIds = [];
        this.roleOnlyIds = [];
        for(let item of this.roleList) {
          if(selectedData === item.roleId) {
            this.queryRoleInfo(item);
            // this.queryMenuTree();
            roleId = item.id;
            roleOnlyId = item.roleId;
          }
        }
        this.roleIds.push(roleId);
        this.roleOnlyIds.push(roleOnlyId);
      }
    },

    /** 修改操作员---查询角色详情 */
    queryRoleInfo(role){
      this.queryRolePermission(role);
      
      this.operatorUpdate.operationPermissionArray = [];
      this.operationPermissions = role.operationPermissions;
      if(this.operationPermissions === 1){
        this.operatorUpdate.operationPermissionArray = ['允许查看','允许操作'];
      } else {
        this.operatorUpdate.operationPermissionArray = ['允许查看'];
      }
    },

    /* 获取角色所具有的菜单权限（通过角色赋权限） */
    queryRolePermission(roleInfo){
      if(roleInfo){
        let param = {
          id:roleInfo.id,
          roleId:roleInfo.roleId,
        }

        // this.isCheck = false;
        api.getSelectedItemPermissions(param).then(
          res => {
            res = res.result;
            let tempId = [];
            let tempNewId = []
            if (res.length > 0) {
              for (let i = 0; i < res.length; i++) {
                tempId.push(parseInt(res[i].id))
                tempNewId.push(res[i].permissionId)
              }
            }
            this.selectedItemPermissions = tempId;
            this.selectedItemNewPermissions = tempNewId;
            this.getTheWholeTree(res);
            // this.getUserChoosedMenuInfo(res);
          }
        )
      } 
    },

    /** 获取全部菜单树（通过角色赋权限） */
    getTheWholeTree(selectItem){
      let param = {
        systemType:'2'
      }

      api.searchMenuList(param).then(
        res => {
          res = res.result;
          this.menuPermission = [];
          for (var i = 0; i < res.length; i++) {
            this.jsonData(res[i])
          }

          /* 获取勾选权限树 element-ui使用 */
          this.selectedItemPermissionsElement = [];
          for (let item of selectItem) {
            if (this.notLeaf.indexOf(item.id) === -1) {
              this.selectedItemPermissionsElement.push(parseInt(item.id))
            }
          }
          
          this.menuPermission = res;
        }
      )
    },

    /**对后台返回数据进行处理 在json数据中添加组件能够识别出来的键值 */
    jsonData(jsonTree){ 
      if ((typeof jsonTree === 'object') && (jsonTree.constructor === Object.prototype.constructor)) {
        var array = []
        array.push(jsonTree)
      } else {
        array = jsonTree
      }
      
      for(let i = 0; i < array.length; i++){
        let jn = array[i];
        if (jn.permissions.length !== 0) {
          this.notLeaf.push(jn.id.toString())
        }
        jn.label = jn.name // 新增title属性 保证Tree组件识别
        jn.children = jn.permissions // 新增children属性 保证Tree组件识别
        jn.expand = true // 新增expand属性 保证菜单展开
        if (this.operatorUpdate.chooseAuthority === '通过角色赋权限') { // '通过角色赋权限' 需要将 '选择框' 置灰
          jn.disabled = true;  
        } else if (this.operatorUpdate.chooseAuthority === '自定义权限') { // '自定义权限'  无需将 '选择框' 置灰
          jn.disabled = false;
        }
        if (jn.children && jn.children.length > 0) {
          this.jsonData(jn.children)
        } else if (jn.parentId === 0) {
          jn.disabled = true
        }
      } 
    },

    /** 操作权限设置（通过角色赋权限） */
    checkAllGroup(checkData){
      if(checkData.length === 2){
        this.operationPermissions = 1;
      } else if (checkData.length === 1) {
        this.operationPermissions = 0;
      }
    },


    /** 查询操作员详情（自定义权限） */
    queryOperatorInfo1(){
      let operatorParam = JSON.parse(sessionStorage.getItem('operatorParam'));

      if (operatorParam) {
        let param = {
          id:operatorParam.id,
          useId:operatorParam.userId,
        }

        api.specificJson(param).then(
          res => {
            console.log("操作员详情", res.result);
            this.operatorUpdate.operatorName = res.result.name;
            
            let roleTemp = {};
            roleTemp.id = res.result.roles[0].roleId;
            roleTemp.roleId = res.result.roles[0].roleNewId;

            let idListTemp = [];
            let roleIdListTemp = [];
            idListTemp.push(res.result.roles[0].roleId);
            roleIdListTemp.push(res.result.roles[0].roleNewId);

            this.queryRolePermission1(roleTemp);
            this.roleSearchById(idListTemp, roleIdListTemp);

            sessionStorage.setItem('operatorInfo', JSON.stringify(res.result))
          }
        )
      }
    },

    /* 获取角色所具有的菜单权限（自定义权限） */
    queryRolePermission1(roleTemp){
      if (roleTemp) {
        let param = {
          id:roleTemp.id,
          roleId:roleTemp.roleId,
        }

        this.isCheck = false;
        api.getSelectedItemPermissions(param).then(
          res => {
            res = res.result;
            let tempId = [];
            let tempNewId = [];
            if (res.length > 0) {
              for (let i = 0; i < res.length; i++) {
                tempId.push(parseInt(res[i].id))
                tempNewId.push(res[i].permissionId)
              }
            }
            this.selectedItemPermissions1 = tempId;
            this.selectedItemNewPermissions1 = tempNewId;
            this.getTheWholeTree1(res);
          }
        )
      }
    },

    /** 获取全部菜单树（自定义权限） */
    getTheWholeTree1(selectItem){
      let param = {
        systemType:'2'
      }

      api.searchMenuList(param).then(
        res => {
          res = res.result;
          this.menuPermission1 = [];
          for (var i = 0; i < res.length; i++) {
            this.jsonData1(res[i])
          }

          /** 获取勾选权限树 element-ui使用 */
          this.selectedItemPermissionsElement1 = [];
          for (let item of selectItem) {
            if (this.notLeaf1.indexOf(item.id) === -1) {
              this.selectedItemPermissionsElement1.push(parseInt(item.id))
            }
          }
          this.menuPermission1 = res;
        }
      )
    },

    /**对后台返回数据进行处理 在json数据中添加组件能够识别出来的键值（自定义权限） */
    jsonData1(jsonTree){ 
      if ((typeof jsonTree === 'object') && (jsonTree.constructor === Object.prototype.constructor)) {
        var array = []
        array.push(jsonTree)
      } else {
        array = jsonTree
      }
      
      for(let i = 0; i < array.length; i++){
        let jn = array[i];
        if (jn.permissions.length !== 0) {
          this.notLeaf1.push(jn.id.toString())
        }
        jn.label = jn.name // 新增title属性 保证Tree组件识别
        jn.children = jn.permissions // 新增children属性 保证Tree组件识别
        jn.expand = true // 新增expand属性 保证菜单展开
        if (this.operatorUpdate.chooseAuthority === '通过角色赋权限') { // '通过角色赋权限' 需要将 '选择框' 置灰
          jn.disabled = true;  
        } else if (this.operatorUpdate.chooseAuthority === '自定义权限') { // '自定义权限'  无需将 '选择框' 置灰
          jn.disabled = false;
        }
        if (jn.children && jn.children.length > 0) {
          this.jsonData1(jn.children)
        } else if (jn.parentId === 0) {
          jn.disabled = true
        }
      } 
    },

    /** 获取全部菜单树(无参，查询空菜单树使用) */
    getTheWholeTree2(){
      let param = {
        systemType:'2'
      }

      api.searchMenuList(param).then(
        res => {
          res = res.result;
          this.menuPermission1 = [];
          for (var i = 0; i < res.length; i++) {
            this.jsonData1(res[i])
          }
          
          this.menuPermission1 = res;
        }
      )
    },

    /** 根据Id查询角色信息 */
    roleSearchById(idListTemp, roleIdListTemp){
      let param = {
        idList: idListTemp,
        roleIdList: roleIdListTemp,
      }

      api.roleSearchById(param).then(
        res => {
          console.log("根据Id查询角色信息", res);
          for (let key in res) {
            this.operationPermissions1 = res[key].operationPermissions;
          }
          
          if (this.operationPermissions1 == 1) {
            this.operatorUpdate.operationPermissionArray1 = ['允许查看','允许操作'];
          } else if(this.operationPermissions1 == 0) {
            this.operatorUpdate.operationPermissionArray1 = ['允许查看'];
          }   
        }
      )
    },

    /** 获取菜单树的全选/半选节点信息（自定义权限） */
    handleCheckChange(click, checked){
      // console.log(checked.checkedKeys);
      // console.log("全选",checked.checkedNodes);
      // console.log(checked.halfCheckedKeys);
      // console.log("半选",checked.halfCheckedNodes);

      let fullChoosed = checked.checkedNodes; // 全选
      let halfChoosed = checked.halfCheckedNodes // 半选

      this.choosedMenuIds = [];
      this.choosedMenuOnlyIds = [];

      for(let full of fullChoosed){
        this.choosedMenuIds.push(full.id);
        this.choosedMenuOnlyIds.push(full.permissionId);
      }
      for(let half of halfChoosed){
        this.choosedMenuIds.push(half.id);
        this.choosedMenuOnlyIds.push(half.permissionId);
      }

      this.isCheck = true;
    },


    /** 操作权限设置（自定义权限） */
    checkAllGroup1(checkData1){
      if (checkData1.length === 2) {
        this.operationPermissions1 = 1;
      } else if (checkData1.length === 1) {
        this.operationPermissions1 = 0;
      }
    },

    /** 修改操作员--取消 */
    cancelUpdateOperator(){
      this.operatorNameLength = 0;
      this.$router.push({
          name:'system-manage-admin'
      })
    },

    /** 确定 修改操作员 */
    confirmUpdateOperator(name){ 
      let operatorInfo = JSON.parse(sessionStorage.getItem('operatorInfo'));
      let operatorParam = JSON.parse(sessionStorage.getItem('operatorParam'));
      if (this.operatorUpdate.chooseAuthority === '通过角色赋权限') {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if (this.isCheck1 == false && operatorParam.roleStatus == 1) {
              this.$Message.info('请选择角色');
              return false;
            }
            if (this.roleIds.length === 0 || this.roleOnlyIds.length === 0) {
              this.$Message.info('请选择角色');
              return false;
            }

            // 由于V2.0版本与BO合在一起，因此会出现在BO新建一个没有赋任何菜单权限的角色，而这种情况在数字录播里面是不能出现的，所以加上以下判断条件
            // 即：如果所选角色没有赋任何菜单权限，则不允许新建/修改操作员
            if (this.selectedItemPermissions.length === 0 || this.selectedItemNewPermissions.length === 0) {
              this.$Message.info('请选择菜单');
              return false;
            }

            let param = {
              id:operatorInfo.id,
              userOnlyId:operatorInfo.userId,
              userName:this.operatorUpdate.operatorName,
              operationPermissions:this.operationPermissions,
              roleIds:this.roleIds,
              roleOnlyIds:this.roleOnlyIds,
              permissionIds:this.selectedItemPermissions,
              permissionOnlyIds:this.selectedItemNewPermissions,
            }

            api.updateOperatorJson(param).then(
              res => {
                this.$Message.success('修改操作员成功!');
                this.roleIds = [];
                this.roleOnlyIds = [];
                this.permissionIds = [];
                this.permissionOnlyIds = [];
                this.operationPermissions = 0;
                this.operatorNameLength = 0;
                this.$router.push({
                  name: 'system-manage-admin'
                })
              },
              err => {
                this.operatorNameLength = 0;
              }
            )
          }
        })
      } else if (this.operatorUpdate.chooseAuthority === '自定义权限') {
        this.$refs[name].validate((valid) => {
          if (this.selectedItemPermissionsElement1.length === 0) {
            if (this.isCheck == false && this.choosedMenuIds.length == 0) {
              this.$Message.info('请选择菜单');
              return false;
            }
            if (this.isCheck == true && this.choosedMenuIds.length == 0) {
              this.$Message.info('请选择菜单');
              return false;
            }

            let param = {
              id:operatorInfo.id,
              userOnlyId:operatorInfo.userId,
              userName:this.operatorUpdate.operatorName,
              operationPermissions:this.operationPermissions1,
              permissionIds:this.choosedMenuIds,
              permissionOnlyIds:this.choosedMenuOnlyIds,
            }

            api.updateOperatorJson(param).then(
              res => {
                this.$Message.success('修改操作员成功！');
                this.roleIds = [];
                this.roleOnlyIds = [];
                this.permissionIds = [];
                this.permissionOnlyIds = [];
                this.operationPermissions1 = 0;
                this.operatorNameLength = 0;
                this.$router.push({
                  name:'system-manage-admin'
                })
              },
              err => {
                this.operatorNameLength = 0;
              }
            )
          } else {
            if (this.isCheck == true && this.choosedMenuIds.length == 0) {
              this.$Message.info('请选择菜单');
              return false;
            }
            if (this.isCheck == true && this.choosedMenuIds.length != 0) {
              let param = {
                id:operatorInfo.id,
                userOnlyId:operatorInfo.userId,
                userName:this.operatorUpdate.operatorName,
                operationPermissions:this.operationPermissions1,
                permissionIds:this.choosedMenuIds,
                permissionOnlyIds:this.choosedMenuOnlyIds,
              }

              api.updateOperatorJson(param).then(
                res => {
                  this.$Message.success('修改操作员成功！');
                  this.roleIds = [];
                  this.roleOnlyIds = [];
                  this.permissionIds = [];
                  this.permissionOnlyIds = [];
                  this.operationPermissions1 = 0;
                  this.operatorNameLength = 0;
                  this.$router.push({
                    name:'system-manage-admin'
                  })
                },
                err => {
                  this.operatorNameLength = 0;
                }
              )
            } else {
              let param = {
                id:operatorInfo.id,
                userOnlyId:operatorInfo.userId,
                userName:this.operatorUpdate.operatorName,
                operationPermissions:this.operationPermissions1,
                permissionIds:this.selectedItemPermissions1,
                permissionOnlyIds:this.selectedItemNewPermissions1,
              }

              api.updateOperatorJson(param).then(
                res => {
                  this.$Message.success('修改操作员成功!');
                  this.roleIds = [];
                  this.roleOnlyIds = [];
                  this.permissionIds = [];
                  this.permissionOnlyIds = [];
                  this.operationPermissions1 = 0;
                  this.operatorNameLength = 0;
                  this.$router.push({
                    name:'system-manage-admin'
                  })
                },
                err => {
                  this.operatorNameLength = 0;
                }
              )
            }
          }
        })
      }



